//
// Header
// --------------------------------------------------

.header-container {
  display: flex;
  align-items: center;
  padding-top: 49px;
  padding-bottom: 51px;
}

.header-logo {
  max-width: 162px;
  width: 100%;
  flex: 0 0 162px;
}

.header-inner {
  display: flex;
  align-items: center;
  width: 100%;
  .header-inner__triangles {
    display: none;
  }
}

.header-nav {
  display: flex;
  column-gap: 60px;
  padding-left: 180px;
  padding-right: 200px;
}

.header-nav__link {
  font-size: 18px;
  line-height: 22px;
  color: $white;
  text-shadow: 0px 0px 8px rgba(171, 239, 255, 0.7);
  text-decoration: none;
  position: relative;
  font-weight: 600;
  &::after {
    content: "";
    position: absolute;
    bottom: -7px;
    left: 0;
    width: 0;
    height: 2px;
    background-color: $white;
    box-shadow: 0px 0px 5px 3px #6c69ff;
    border-radius: 100%;
    opacity: 0;
    transition: 0.25s;
  }
  @include hover {
    color: $white;
    text-decoration: none;
    &::after {
      opacity: 0.9;
      width: 100%;
    }
  }
  &.active {
    &::after {
      content: "";
      position: absolute;
      bottom: -7px;
      left: 0;
      width: 100%;
      height: 2px;
      background-color: $white;
      box-shadow: 0px 0px 5px 3px #6c69ff;
      border-radius: 100%;
      opacity: 0;
      transition: 0.25s;
    }
  }
}

.header-languages {
  margin-left: auto;
  position: relative;
  margin-right: 50px;
  .header-languages__current {
    display: flex;
    align-items: center;
    color: $white;
    cursor: pointer;
    position: relative;
    > img {
      width: 10px;
      height: 5px;
      position: absolute;
      right: -15px;
      transition: transform 0.25s;
    }
    .header-languages__link {
      pointer-events: none;
      user-select: none;
    }
    @include hover {
      > img {
        transform: rotate(180deg);
      }
    }
  }
  .header-languages__dropdown {
    position: absolute;
    top: 100%;
    left: 50%;
    transform: translateX(-50%) translateY(25px);
    background: linear-gradient(180deg, #0f0a39 0%, #0a0631 104.91%);
    opacity: 0;
    transition: 0.25s;
    .header-languages__link {
      padding: 9px 18px 11px;
      border-radius: 8px;
      opacity: 0.6;
      transition: background 0.25s;
      @include hover {
        background: rgba(255, 255, 255, 0.2);
        opacity: 1;
      }
      &.active {
        background: rgba(255, 255, 255, 0.2);
        opacity: 1;
      }
    }
    &::after {
      content: "";
      position: absolute;
      inset: 0;
      border-radius: 8px;
      padding: 1px;
      background: linear-gradient(
        45deg,
        #cafa7a,
        #abf5ff,
        #ff60ac,
        #5d81ff,
        #4cff7e
      );
      -webkit-mask: linear-gradient(#fff 0 0) content-box,
        linear-gradient(#fff 0 0);
      -webkit-mask-composite: xor;
      mask-composite: exclude;
      opacity: 0.3;
      z-index: -1;
    }
  }
  .header-languages__link {
    display: block;
    font-size: 18px;
    line-height: 22px;
    color: $white;
    text-decoration: none;
    z-index: 2;
    user-select: none;
  }
}

.header-languages {
  &.active {
    .header-languages__dropdown {
      opacity: 1;
      transform: translateX(-50%) translateY(15px);
    }
    .header-languages__current {
      > img {
        transform: rotate(180deg);
      }
    }
  }
}

.header-toggle {
  display: none;
}

@media (max-width: 1650px) {
  .header-container {
    padding-left: 40px;
    padding-right: 40px;
    max-width: 100%;
  }
  .header-logo {
    max-width: 132px;
    flex: 0 0 132px;
  }
  .header-nav {
    padding-left: 10%;
    padding-right: 10%;
    column-gap: 40px;
  }
  .header-nav__link {
    font-size: 16px;
    line-height: 19px;
  }
  .header-languages {
    .header-languages__link {
      font-size: 14px;
      line-height: 17px;
    }
  }
  .header-shop {
    margin-left: auto;
  }
}

@media (max-width: 1365px) {
  .header-container {
    padding-top: 20px;
    padding-bottom: 20px;
  }
  .header-logo {
    max-width: 100px;
    flex: 0 0 100px;
  }
  .header-nav {
    padding-left: 10%;
    padding-right: 15%;
    column-gap: 35px;
  }
  .header-nav__link {
    font-size: 12px;
    line-height: 14px;
  }
}

@media (max-width: 1210px) {
  .header-nav {
    padding-left: 5%;
    padding-right: 5%;
  }
}

