  .header-container {
    justify-content: space-between;
    padding: 32px 40px 25px;
  }
  .header-nav {
    display: block;
    padding: 0;
    order: 2;
    padding-top: 80px;
    flex: 1;
    overflow: auto;
    width: 100%;
    padding-bottom: 40px;
    .header-nav__link {
      display: block;
      width: 100%;
      font-size: 36px;
      line-height: 43px;
      text-align: center;
      margin-bottom: 40px;
      @include hover {
        &::after {
          opacity: 0;
          width: 0;
        }
      }
    }
  }
  .header-shop {
    order: 1;
    margin-left: 0;
  }
  .header-connect {
    order: 3;
  }
  .header-languages {
    position: absolute;
    top: 36px;
    left: 40px;
  }
}

@media (max-width: 575px) {
  .header-container {
    padding: 25px 20px;
  }
  .header-inner {
    padding-top: 20px;
    padding-bottom: 20px;
  }
  .header-nav {
    padding-top: 40px;
    .header-nav__link {
      font-size: 28px;
      line-height: 34px;
      margin-bottom: 20px;
    }
  }
  .header-languages {
    top: 31px;
    left: 20px;
  }
}
